import React from 'react';
import ReactDOM from 'react-dom';
import { Dashboard } from './dashboard';
import './mdh-index.css';

class App extends React.Component {
    
    render () {
        
        return (
            <Dashboard />
        )
    }
}

ReactDOM.render(

    <App />,
    document.getElementById( 'root' )
);





